@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
      width: 70px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }

  @media (max-width: 650px) {
    padding: 25 px 0;
    .container {
      flex-direction: column;
    }

    .header__cart {
      margin-top: 20px;
      min-width: 70%;
      a {
        justify-content: center;
      }
    }
  }
}
